import {Component, Input, OnInit} from '@angular/core';
import {FormGroup, Validators} from '@angular/forms';
import {MessageTypes} from 'app/shared/enums/message-types.enum';
import {IFileData} from 'app/shared/interfaces/files';
import {IResponse} from 'app/shared/interfaces/responces';
import {AppHelpers} from 'app/shared/resources/app-helpers';
import {ICourseAttachmentModel, ICourseAttachmentParamsRequest,} from 'app/training/models/course/attachment';
import {CourseAttachmentService} from 'app/training/services/course/course-attachment.service';

@Component({
  selector: "app-course-attachments",
  templateUrl: "./course-attachments.component.html",
  styleUrls: ["./course-attachments.component.scss"],
})
export class CourseAttachmentsComponent extends AppHelpers implements OnInit {
  @Input() courseId: number;

  form: FormGroup;
  submitted: boolean = false;

  isLoaded: boolean = false;

  // existingAttachments: ICourseAttachmentModel[] = [];
  files: IFileData[] = [];
  removeFiles: boolean = false;
  addAttachParams: ICourseAttachmentParamsRequest;

  constructor(private courseAttachmentService: CourseAttachmentService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      attachmentTypeId: [null, [Validators.required]],
      file: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.checkNoBusinessOrCompany() && this.courseId > 0) this.getAll();
    else this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.courseAttachmentService
      .getAllByCourseId(this.courseId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICourseAttachmentModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          // this.existingAttachments = res.data;
          this.files = [];
          res.data.forEach((attach) => {
            this.files.push({
              id: attach.id,
              name: attach.en_Name,
              path: "/" + attach.path,
              size: attach.size,
              type: attach.mime,
            });
          });
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    this.removeFiles = false;
    // if (this.form.valid) {
    //   const model: ICourseAttachmentRequest = {
    //     files: this.formCtrls.file.value,
    //   };
    //   // console.log(model);
    //   const formData = HelpersFunctions.objectToFormData(model);

    //   let params: ICourseAttachmentParamsRequest = {
    //     courseID: this.courseId,
    //     AttachmentTypeID: this.formCtrls.attachmentTypeId.value,
    //   };

    //   this.create(formData, params);
    // } // else this.toastr.error("General.formNotValid");
  }

  // create(model: FormData, params: ICourseAttachmentParamsRequest) {
  //   this.showLoading();
  //   this.courseAttachmentService
  //     .create(model, params)
  //     .subscribe((res: IResponse<ICourseAttachmentModel>) => {
  //       if (res?.message?.messageType == MessageTypes.Success) {
  //         this.clearForm();
  //         this.addSuccessfullyMsg(res?.message?.messageTypeName);
  //         this.getAll();
  //       } else this.errorList(res.message.messages);
  //       this.hideLoading();
  //     });
  // }

  afterCreate() {
    this.clearForm();
    this.getAll();
  }

  // onDelete(courseAttachment: ICourseAttachmentModel): void {
  //   console.log(courseAttachment);
  //   let msg = courseAttachment.en_Name;
  //   this.deleateDialog(msg).subscribe((confirm) => {
  //     if (confirm) this.delete(courseAttachment);
  //   });
  // }

  delete(attach: IFileData) {
    if (attach.id) {
      this.showLoading();
      this.courseAttachmentService
        .Delete(attach.id)
        .subscribe((res: IResponse) => {
          if (res.message.messageType == MessageTypes.Success) {
            this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
            this.getAll();
          } else this.errorList(res.message.messages);
          this.hideLoading();
        });
    }
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
    this.removeFiles = true;
  }

  changeAddParams() {
    this.addAttachParams = {
      courseID: this.courseId,
      AttachmentTypeID: this.formCtrls.attachmentTypeId.value,
    };
  }
}
