import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IResponse } from "app/shared/interfaces/responces";

import { ICourseGeneralRequest } from "app/training/models/course/general";
import { ICourseModel } from "app/training/models/course/course";

@Injectable({
  providedIn: "root",
})
export class CourseGeneralService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICourseGeneralRequest) {
    return this.post<IResponse<ICourseModel>>(
      "Training/Course/Create",
      data
    );
  }

  update(data: ICourseGeneralRequest) {
    return this.post<IResponse>(
      "Training/Course/Edit",
      data
    );
  }

}
