import { Component, OnInit } from "@angular/core";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MenuItem } from "primeng/api";

@Component({
  selector: "app-add-course",
  templateUrl: "./add-course.component.html",
  styleUrls: ["./add-course.component.scss"],
})
export class AddCourseComponent extends AppHelpers implements OnInit {
  items: MenuItem[];
  activeIndex: number = 0;

  courseId: number = 0;

  constructor() {
    super();
  }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((params) => {
      this.courseId = Number(params.get("id")) ?? 0;
    });

    this.items = [
      {
        label: "General",
      },{
        label: "TSP & Instructor",
      },
      {
        label: "Course Competence",
      },
      {
        label: "Outline & Attachments",
      },

    ];
  }

  next() {
    if (this.activeIndex < this.items.length - 1) {
      this.activeIndex++;
    }
  }

  back() {
    if (this.activeIndex > 0) {
      this.activeIndex--;
    }
  }
}
