import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ICourseAttachmentModel,
  ICourseAttachmentParamsRequest,
} from "app/training/models/course/attachment";

@Injectable({
  providedIn: "root",
})
export class CourseAttachmentService extends AppClientService {
  constructor() {
    super();
  }

  // FormData > ICourseAttachmentRequest
  create(data: FormData, params: ICourseAttachmentParamsRequest) {
    return this.post<IResponse<ICourseAttachmentModel>>(
      "Training/Course/AddCourseAttachments",
      data,
      {
        params: params,
      }
    );
  }

  getAllByCourseId(courseId: number) {
    return this.get<IResponse<ICourseAttachmentModel[]>>(
      "Training/Course/GetAllCourseAttachments",
      {
        params: {
          courseID: courseId,
        },
      }
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("Training/Course/DeleteCourseAttachments", {
      params: { AttachmentID: id },
    });
  }
}
