import { msgTranslate } from "app/shared/resources/helpers-functions";
import { ICourseCompetenceModel } from "../models/course/competence";

export function courseCompetenceToDisplay(
  jobCareers: ICourseCompetenceModel[]
): ICourseCompetenceModel[] {
  return jobCareers.map((courseCompetence) => {
    return <ICourseCompetenceModel>{
      ...courseCompetence,
      competence: msgTranslate(
        courseCompetence.core_Competence_Detail?.core_Competence?.ar_Name,
        courseCompetence.core_Competence_Detail?.core_Competence?.en_Name
      ),
      competenceLevel: msgTranslate(
        courseCompetence.core_Competence_Detail?.core_Competence_Level?.ar_Name,
        courseCompetence.core_Competence_Detail?.core_Competence_Level?.en_Name
      ),
    };
  });
}



