import { Injectable } from "@angular/core";
import { IResponse } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  ICourseCompetenceDeleteParams,
  ICourseCompetenceModel,
  ICourseCompetenceRequest,
} from "app/training/models/course/competence";
import { db } from "app/shared/resources/database-tabels";

@Injectable({
  providedIn: "root",
})
export class CourseCompetenceService extends AppClientService {
  constructor() {
    super();
  }

  create(data: ICourseCompetenceRequest) {
    return this.post<IResponse<ICourseCompetenceModel>>(
      "Training/course/AddCompetenceDetailToCourse",
      data
    );
  }

  getAllByCourseId(courseId: number) {
    return this.get<IResponse<ICourseCompetenceModel[]>>(
      "Training/Course/GetAllCourseCompetenceData",
      {
        params: {
          $filter: `course_id eq ${courseId}`,
          $expand: `${db.core_Competence_Detail}.${db.core_Competence},${db.core_Competence_Detail}.${db.core_Competence_Level}`,
        },
      }
    );
  }

  Delete(params: ICourseCompetenceDeleteParams) {
    return this.delete<IResponse>(
      "Training/Course/DeleteCourseCompetenceDetail",
      {
        params: params,
      }
    );
  }
}
