import { Component, OnInit, Input } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";
import { MatTableDataSource } from "@angular/material/table";
import {
  ICourseCompetenceDeleteParams,
  ICourseCompetenceModel,
  ICourseCompetenceRequest,
} from "app/training/models/course/competence";
import { CourseCompetenceService } from "app/training/services/course/course-competence.service";
import { CompetenceService } from "app/org-management/services/competence.service";
import { ICompetenceLevelDetailsModel } from "app/org-management/model/competence";
import { courseCompetenceToDisplay } from "app/training/mapper/course-competence";

@Component({
  selector: "app-course-competence",
  templateUrl: "./course-competence.component.html",
  styleUrls: ["./course-competence.component.scss"],
})
export class CourseCompetenceComponent extends AppHelpers implements OnInit {
  @Input() courseId: number;

  form: FormGroup;
  submitted: boolean = false;

  mandatoryColomns: string[] = [
    "Ser",
    // "id",
    "competence",
    "competenceLevel",
    "actions",
  ];
  optionalColomns: string[] = [];
  isLoaded: boolean = false;

  dataSource: MatTableDataSource<ICourseCompetenceModel>;

  constructor(
    private courseCompetenceService: CourseCompetenceService,
    private competenceService: CompetenceService
  ) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      competenceId: [null, [Validators.required]],
      competenceLevelId: [null, [Validators.required]],
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    if (this.dataSource?.data?.length > 0)
      this.dataSource = new MatTableDataSource(null);
    if (this.checkNoBusinessOrCompany() && this.courseId > 0) this.getAll();
    else this.isLoaded = true;
  }

  get formCtrls() {
    return this.form.controls;
  }

  getAll() {
    this.courseCompetenceService
      .getAllByCourseId(this.courseId)
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICourseCompetenceModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          this.dataSource = new MatTableDataSource(
            courseCompetenceToDisplay(res.data)
          );
          this.isLoaded = true;
        } else this.errorList(res.message.messages);
      });
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: ICourseCompetenceRequest = {
        course_ID: this.courseId,
        competence_Detail_ID: 0,
      };

      this.getCompetenceDetailsId(model);
    } // else this.toastr.error("General.formNotValid");
  }

  getCompetenceDetailsId(model: ICourseCompetenceRequest) {
    this.showLoading();
    this.competenceService
      .getDetailsByCompetenceAndLevelIds(
        this.form.value.competenceId,
        this.form.value.competenceLevelId
      )
      .pipe(this.cancelRequest())
      .subscribe((res: IResponse<ICompetenceLevelDetailsModel[]>) => {
        if ((res.message.messageType = MessageTypes.Success)) {
          // [this.levelDetails] = res.data.slice(-1);
          model.competence_Detail_ID = res.data[0].id;
          if (model.competence_Detail_ID > 0) this.create(model);
        } else this.errorList(res.message.messages);
      });
  }

  create(model: ICourseCompetenceRequest) {
    this.courseCompetenceService
      .create(model)
      .subscribe((res: IResponse<ICourseCompetenceModel>) => {
        if (res?.message?.messageType == MessageTypes.Success) {
          this.clearForm();
          this.addSuccessfullyMsg(res?.message?.messageTypeName);
          this.getAll();
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  onDelete(competence: ICourseCompetenceModel): void {
    let msg = competence.competence + " - " + competence.competenceLevel;
    this.deleateDialog(msg).subscribe((confirm) => {
      if (confirm) this.delete(competence);
    });
  }

  delete(competence: ICourseCompetenceModel) {
    let params: ICourseCompetenceDeleteParams = {
      CourseId: this.courseId,
      CompetenceDetailId: competence.competence_Detail_ID,
    };
    this.showLoading();
    this.courseCompetenceService.Delete(params).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
        this.getAll();
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  clearForm() {
    this.form.reset({ id: 0 });
    this.submitted = false;
  }
}
